* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: gray;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: bold;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 15px 50px 0;
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

.logo img {
  width: auto;
  height: 40px;
}

.nav {
  align-items: center;
  display: flex;
}

.nav_list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_item {
  margin: 0 0 0 20px;
}

.nav_item:first-child {
  margin-left: 0;
}

.nav_item:last-child {
  margin-right: 0;
}

.nav_link {
  color: #000;
  text-transform: uppercase;
  text-align: center;
  height: 50px;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 50px;
  text-decoration: none;
  display: block;
  position: relative;
}

.is-active {
  color: #00acdc;
  text-decoration: none;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  width: 100%;
  max-width: 60px;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.nav_link:hover {
  color: #00acdc;
}
